import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormControl } from '@angular/forms';

import { EmailService } from '../../angular-services/email.service';

@Component({
  selector: 'app-sugerir-curso',
  templateUrl: './sugerir-curso.component.html',
  styleUrls: ['./sugerir-curso.component.css']
})
export class SugerirCursoComponent implements OnInit {

  formEmail: FormGroup;

  nome: string;
  email: string;
  telefone: string;
  celular: string;
  estado: string;
  cidade: string;
  curso: string;
  mensagem: string;
  resposta: string;
  tipoResposta: string;

  constructor(private emailService: EmailService) { }

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.formEmail = new FormGroup({
      name: new FormControl('', Validators.required),
      email: new FormControl('', Validators.required),
      phone: new FormControl('', Validators.required),
      message: new FormControl('', Validators.required)
    });
  }

  sendEmail() {
    if (this.formEmail.valid) {
      let data = this.formEmail.value;
      let date = new Date();
      let body = `
        Nome: ${data.name}
        Email: ${data.email}
        Celular: ${data.phone}
        Mensagem: ${data.message}
        Data: ${date}
      `;
      this.emailService.sendEmail(body, 'Sugestão de Curso').subscribe(
        response => {
          if(response) {
            this.resposta = response.message;
            if (response.status == 'success') {
              this.tipoResposta = response.status;
            } else {
              this.tipoResposta == 'danger';
            }
          }else{
            this.tipoResposta = 'danger';
            this.resposta = 'Falha ao enviar o email. Tente novamente em instantes.';
          }
        }
      )
    }
  }

}
