import { Component, OnInit, EventEmitter, Output } from '@angular/core';

import { CourseService } from '../../angular-services/course.service';
import { CourseCategoryService } from '../../angular-services/course-category.service';
import { Course } from '../../classes/course';
import { CourseCategory } from '../../classes/course-category';

@Component({
  selector: 'app-cursos',
  templateUrl: './cursos.component.html',
  styleUrls: ['./cursos.component.css']
})
export class CursosComponent implements OnInit {

  @Output() loading = new EventEmitter<boolean>();
  courses: Course[] = [];
  coursesPerCategory: Course[] = [];
  categories: CourseCategory[] = [];
  categorySelected: CourseCategory;

  constructor(private courseService: CourseService, private courseCategoryService: CourseCategoryService) { }

  ngOnInit() {
    this.getCourses();
    this.getCategories();
  }

  getCourses() {
    this.loading.emit(true);
    this.courseService.getCourses().subscribe(
      response => {
        this.courses = response;
        this.coursesPerCategory = response;
        this.loading.emit(false);
      }
    );
  }

  getCategories() {
    this.loading.emit(true);
    this.courseCategoryService.getCategories().subscribe(
      response => {
        this.loading.emit(false);
        this.categories = response;
      }
    );
  }

  selectAll() {
    this.coursesPerCategory = this.courses;
    this.categorySelected = null;
  }

  selectCategory(category){
    if(category != this.categorySelected) {
      this.categorySelected = category;
      this.coursesPerCategory = [];
      for (let course of this.courses) {
        if (course.category._id == category._id) {
          this.coursesPerCategory.push(course);
        }
      }
    }
  }

}

