import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CursosAreaComponent } from './cursos-area.component';

const routes: Routes = [
  { path: '', component: CursosAreaComponent },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class CursosAreaRoutingModule { }
